import { Col, Row } from "react-bootstrap"

const MemberList = ({ members }) => {
	return <Row>
		{members.map(({name, titles}) => <Col 
		md="6" xxl="4" key={name}
		className="mb-2"
		>
			<Row>
				<Col sm="6" xxl="5" className="fw-bold fs-6">{name}</Col>
				<Col sm="6" xxl="7">
					<ul className="fw-light small">
						{titles.map(el => <li key={el}>{el}</li>)}
					</ul>
				</Col>
			</Row>
		</Col>)}
	</Row>
}

export default MemberList