
import { useCallback, useState } from "react"
import { Accordion, Col, Row, Button } from "react-bootstrap"
import Sidebar from "../../Sidebar"
import "./style.css"

const AccordionPage = ({
	title,
	introduction,
	accordionItems
}) => {
	const [activeKey, setActiveKey] = useState([0])
	const [expandAllStatus, setExpandAllStatus] = useState(accordionItems.length <= 1)
	const handleExpandAll = () => {
		console.log(activeKey)

		if (!expandAllStatus) {
			setActiveKey(accordionItems.map((item, index) => index))
		}else{
			setActiveKey([])
		}		
		setExpandAllStatus(!expandAllStatus)
	}
	
	const toggleExpand = useCallback((eventKey) => {
		
		const keyExists = activeKey.some(k => k === eventKey)
		const activeKey_tem = keyExists ? activeKey.filter(k => k !== eventKey) : [...activeKey, eventKey]
		setActiveKey(activeKey_tem)
		if (activeKey_tem.length === accordionItems.length) {
			setExpandAllStatus(true)
		} else {
			setExpandAllStatus(false)
		}
	}, [activeKey, setActiveKey])

	return <>
		<Row>
			<Col lg="10">
				<h1>{title}</h1>
				{!Array.isArray(introduction) 
					? <p className="lead">{introduction}</p>
					: introduction.map(text => <p className="lead">{text}</p>)
				}
				
				<div>
					<div className="d-flex flex-row-reverse">
						<Button variant="secondary mb-2" onClick={handleExpandAll}>{!expandAllStatus ? "Expand All": " Fold All"}</Button>
					</div>
					<Accordion activeKey={activeKey}>
						{accordionItems.map(({title, content}, index) => <Accordion.Item eventKey={index} key={title}>
							<Accordion.Button onClick={() => toggleExpand(index)}>{title}</Accordion.Button>
							<Accordion.Body>
								{content}
							</Accordion.Body>
						</Accordion.Item>)}
					</Accordion>
				</div>
			</Col>
			<Sidebar />
		</Row>

	</>
}

export default AccordionPage